<template>
  <el-upload
      :class="{ hide: hideUpload }"
      action="https://run.mocky.io/v3/9d059bf9-4660-45f2-925d-ce80ad6c4d15"
      list-type="picture-card"
      :limit="upload_image_limit"
      :file-list="list_images"
      :show-file-list="true"
      :on-change="fileChange"
      :auto-upload="false"
      :on-progress="handle_progress_images_upload"
      accept="image/*"
      multiple
    >
      <el-icon>
        <Plus />
      </el-icon>
      <template #file="{ file }">
        <div>
          <img class="el-upload-list__item-thumbnail" :src="file.url" alt />
          <span class="el-upload-list__item-actions">
            <span class="el-upload-list__item-preview" @click="handlePictureCardPreview(file)">
              <el-icon>
                <zoom-in />
              </el-icon>
            </span>
            <span class="el-upload-list__item-delete" @click="handleRemove(file)">
              <el-icon>
                <Delete />
              </el-icon>
            </span>
          </span>
          <el-progress type="circle" :percentage="percentage" :color="colors" class="progress" width="70" style="display:none;"/>
        </div>
      </template>
    </el-upload>

    <el-dialog v-model="dialogVisible">
      <img :src="dialogImageUrl" alt="Preview Image" style="max-width: 100%"/>
    </el-dialog>

</template>

<script setup>
import { ref, defineExpose } from 'vue'
import { Delete, Plus, ZoomIn } from "@element-plus/icons-vue";

// import { ElMessage } from "element-plus";
const upload_image_limit = ref(9); //图片限制数量
const dialogImageUrl = ref(""); // 预览图url
const dialogVisible = ref(false); // 预览弹窗
const hideUpload = ref(false); // 是否隐藏上传按钮
const list_images = ref([]); // 文件列表
const percentage = ref(90)   //上传进度
const colors = [                  // 上传颜色进度条
  { color: '#f56c6c', percentage: 20 },
  { color: '#e6a23c', percentage: 40 },
  { color: '#5cb87a', percentage: 60 },
  { color: '#1989fa', percentage: 80 },
  { color: '#6f7ad3', percentage: 100 },
]

// 更新上传加号按钮显示状态
function updateUploadShown() {
  hideUpload.value = list_images.value.length === upload_image_limit.value;
}
// 文件改变
function fileChange(file, resfileList) {
  list_images.value = resfileList;
  updateUploadShown();
}
// 移除图片
const handleRemove = (file) => {
  const list = list_images.value;
  for (const i in list) {
    if (list[i].uid === file.uid) {
      list.splice(i, 1);
    }
  }
  list_images.value = list;
  updateUploadShown();
};
// 预览图片
const handlePictureCardPreview = (file) => {
  dialogImageUrl.value = file.url;
  dialogVisible.value = true;
};

//图片上传进度
const handle_progress_images_upload = (file) => {
  console.info(file)
};

defineExpose({list_images, })
</script>

<style scoped>
/*控制上传控件大小*/
:deep(.el-upload--picture-card){
  margin-top: 4%;
  height:80px;
  width:80px;
  line-height: 80px;
}
/*控制缩略图控件大小*/
:deep(.el-upload-list__item){
  margin-top: 4%;
  height:80px;
  width:80px;
  line-height: 80px;
}
/*控制超过指定数量时隐藏上传按钮*/
.hide >>> .el-upload--picture-card {
  display: none;
}

/* 内容居中 */
.progress{
  display: flex;
  align-items: center;
  justify-content: center;
}

</style>